import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { myFetcher } from './fetchAction';

const CommentsComponent = () => {

	//curl -v https://cdn.contentful.com/spaces/ji9dy7eny2j0/entries?access_token=ln4N7UPRHV88qB9CISKcXd-D5VQ4_Sx-iWkauQKUxJo












	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	//const URL = 'http://localhost:2001/comments.php'
	const URL = 'https://cdn.contentful.com/spaces/ji9dy7eny2j0/entries?access_token=ln4N7UPRHV88qB9CISKcXd-D5VQ4_Sx-iWkauQKUxJo'
	const isOnline = true;//(!navigator.onLine) ? false : true;

	/// Post/Save comment to DB.
	const postComment = async (e) => {
		const textArea = (e.target.previousElementSibling) ? e.target.previousElementSibling : e.target.parentNode.previousElementSibling

		const textStr = textArea.value
		if(textStr === '') return;

		const date = new Date().toString().substring(0,24)
		const user = 'anon'
		const sendStr = {strText: textStr, dateCreated:date , createdBy:user}
		const newData = [...hasData.comments, sendStr]

		async function saveToDB(){
			const URL = "http://localhost:2001/indexB.php";
			const options = {
				method: "POST",
				headers: { "Accept": "application/json"},
				body: JSON.stringify({text : newData})
			}
			const rawSend = await fetch(URL, options);
			return await rawSend.text();
		}

		const response = await saveToDB();

		if(response.substr(0,7) === "success"){
			// Forces update of render.
			dispatch({ type: 'APP_GOT_DATA', payload: {comments:newData} } )
		} else {
			console.log(response)
			alert("Not Saved !!")
		}
	}

	/// No internet - abort.
	if(!isOnline){
		return ( <div id='comments' className=''>No internet connection.</div>)
	}

	/// Fetching data.
	if( ! hasData ){
		myFetcher(URL, dispatch)
		return (<div id='comments' >Loading...</div>)
	}

	/// Request returned error.
	if( hasData === 'Error' ){
		/** TODO: in here - load dummy data? */
		return (<div id='comments' >Error loading content.</div>)
	}

	/// Data OK - populate component content.
	if( hasData !== 'Error'){

		const retrievedArray = hasData.items

		//retrievedArray.forEach(item => console.log(item.fields));

		//console.log();


		//return (<div>empty</div>)
console.log(retrievedArray)


		const sortedItems =  retrievedArray.sort((a, b) => {
			return Date.parse(new Date(b.fields.dateCreated)) - Date.parse(new Date(a.fields.dateCreated))
		})

		const itemsList = sortedItems.map((item, i) => {

			const d = new Date(item.fields.dateCreated);
			const formattedDate = d.toUTCString().substring(0, d.toUTCString().length -4);

			return(
				<div key={i} className="commentBox" >
					<span style={{fontWeight:"bold"}}>{item.fields.createdBy} : </span>
					<span style={{color:"dodgerblue"}}> {formattedDate}</span><br />
					<div style={{paddingLeft:"0px", paddingTop:"2px"}}>{item.fields.strText}</div>
				</div>
			)
		})

		return (
			<div id='comments' >

				<div id="newComments">
					<textarea id="addCommentTextArea" style={{ height:"100px"}} placeholder="Add a comment ..."></textarea>
					<span className="right green-text text-darken-2" id="postComment" onClick={postComment}>
						<i className="material-icons waves-effect  waves-light" >send</i>
					</span>
				</div>

				{ itemsList }
			</div>
			)

		//const sortedItems =  hasData.comments.sort((a, b) => {
			//return Date.parse(new Date(b.dateCreated)) - Date.parse(new Date(a.dateCreated))
		//})

		//const itemsList = sortedItems.map((item, i) => {
			//return(
				//<div key={i} className="commentBox" >
					//<span style={{fontWeight:"bold"}}>{item.createdBy} : </span>
					//<span style={{color:"dodgerblue"}}> {item.dateCreated}</span><br />
					//<div style={{paddingLeft:"0px", paddingTop:"2px"}}>{item.strText}</div>
				//</div>
			//)
		//})

		//return (
			//<div id='comments' >

				//<div id="newComments">
					//<textarea id="addCommentTextArea" style={{ height:"100px"}} placeholder="Add a comment ..."></textarea>
					//<span className="right green-text text-darken-2" id="postComment" onClick={postComment}>
						//<i className="material-icons waves-effect  waves-light" >send</i>
					//</span>
				//</div>

				//{ itemsList }
			//</div>
			//)





	}

}

export default CommentsComponent



